<template name="el-zoom-in-center">
  <div class="transition-box Con">
    <div class="inner">
    <!-- {{this.$route.query.searchVal}} -->
      <ol class="content">
        <h2>共搜索到与「{{this.$route.query.searchVal}}」相关的游戏{{ num }}个</h2>
        <li v-for="(v,i) in items" :key="i">
          <div class="imgCon">
            <img src="@/assets/img/erzhan.jpg" alt="">
          </div>
          <div class="desCon">
            <div class="btn">
              <router-link class="viewBtn" :to="{path:'/detail', query:{'id':v.id}}" title="查看">查看</router-link>
            </div>
            <h3>挺我</h3>
            <h5>3.1万人安装</h5>
            <p>健身很重要，有一个好的健身房很重要，有一个好教练也很重要，但是需要钱，更需要时间。 《打卡》为你提供在线健身房，时间灵活，没有成本，真人教练指导，远程闹钟提醒，打卡点评督促。无论是为</p>
          </div>
        </li>
      </ol>
    </div>
  </div>
</template>
<script>
export default {
  mounted () {
    console.log(this.$route.query.searchVal)
  },
  data: function () {
    return {
      num: 0,
      items: [{
        name: '1',
        id: 1
      }, {
        name: '3',
        id: 2
      }]
    }
  }
}
</script>

<style lang="scss" scoped>
.Con{
  background: #f5f5f5;
  .inner{
    background: white;
    .content{
      padding: 30px;
      text-align: left;
      h2{
        font-size: 16px;
        margin-bottom: 20px;
      }
      li{
        height: 90px;
        margin-bottom: 10px;
        .imgCon{
          float: left;
          width: 70px;
          border-radius: 10px;
          overflow: hidden;
        }
        .desCon{
          position: relative;
          margin-left: 80px;
          .btn{
            position: absolute;
            right: 0;
            a.viewBtn{
              float: right;
              width: 64px;
              color: white;
              background: #24aa42;
              text-align: center;
              font-size: 12px;
              height: 26px;
              line-height: 26px;
              border-radius: 4px;
            }
          }
          h3{
            font-size: 16px;
            color: #333
          }
          h5{
            font-size: 12px;
            color: #999;
            height: 12px;
          }
          p{
            font-size: 12px;
            color: #555;
            padding-right: 12px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
          }
        }
      }
    }
  }
}
</style>



